var qs = require('querystring');
var encryption = require('../../utils/Crypto.js');


/**
 *  gravatar
 * @param email {String} 
 * @param [options] {Object} 
 * @param [options.origin="http://gravatar.duoshuo.com/avatar/"] {String} 
 * @param [options.size=100] {Number} 
 * @param [options.default="retro"] {Number} 
 * @param [options.forcedefault=false] {*} 
 * @param [options.rating=null] {*} 
 * @returns {string}
 */
exports.gravatar = function (email, options) {
    options = options || {};
    email = email.toLowerCase();

    if (!options.origin) {
        options.origin = 'http://cn.gravatar.com/avatar/';
    }

    options.origin += encryption.md5(email) + '?';

    if (!options.size) {
        options.size = 100;
    }

    if (!options.default) {
        //options.default = 'http://s.ydr.me/p/i/avatar.png';
        options.default = 'retro';
    }

    if (options.forcedefault) {
        options.forcedefault = 'y';
    } else {
        options.forcedefault = false;
    }

    var query = {
        s: options.size
    };

    if (options.default) {
        query.d = options.default;
    }

    if (options.forcedefault) {
        query.f = options.forcedefault;
    }

    if (options.rating) {
        query.r = options.rating;
    }

    return options.origin + qs.stringify(query);
};




