var lang = {
    minLength: {
        input: `'${1}khng c nh hn ${2} k t'`,
        select: `'${1} t nht phi chn ${2} mc'`
    },
    maxLength: {
        input: `'${1} khng c ln hn ${2} k t'`,
        select: `'${1} nhiu nht ch c th chn ${2} mc '`
    },
    least: `'${1} t nht phi chn ${2} mc'`,
    most: `'${1}nhiu nht ch c th chn ${2} mc'`,
    type: {
        array: `'${1} bt buc phi l gi tr Array '`,
        number: `'${1} phi l s '`,
        integer: `'${1} phi l s chn.'`,
        mobile: `'${1} phi l format s in thoi '`,
        email: `'${1}phi l format email '`,
        url: `'${1} phi l a ch website '`,
        date: `'${1} invalid date'`,
        numreicAllowZero: `'${1} invalid'`
    },
    required: `'${1} khng c rng.'`,
    equal: `'${1} v ${2} phi ging nhau.'`,
    min: `'${1} khng nh hn ${2}'`,
    max: `'${1} khng ln hn ${2}'`,
    step: `'${1} gi tr k tip l ${2}'`
};


/**
 *  lang
 * @param type
 * @param [category]
 * @returns {*}
 */
exports.get = function (type, category) {
    var la = lang[type];

    if (!category) {
        return la;
    }

    return ((la && la[category]) || '');
};


/**
 *  lang
 * @param type
 * @param msg
 * @param [category]
 */
exports.set = function (type, msg, category) {
    if (category) {
        lang[type] = lang[type] || {};
        lang[type][category] = msg;
    } else {
        lang[type] = msg;
    }
};
